@mixin fixedAll($position) {
  position: $position;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

@mixin positionMiddle($position) {
  position: $position;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}

@mixin transform($transformValue) {
  transform: $transformValue;
  -moz-transform: $transformValue;
  -webkit-transform: $transformValue;
  -o-transform: $transformValue;
}

@mixin flexMiddle {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin textellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@mixin flex-fix() {
  -ms-flex: 1 1 0.000000001px;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  -webkit-flex-basis: 0.000000001px;
  flex-basis: 0.000000001px;
  width: 1%;
}

@mixin square($size) {
  width: $size;
  height: $size;
}

@mixin rect($width, $height) {
  width: $width;
  height: $height;
}

@mixin fixSquare($size) {
  width: $size;
  padding-bottom: $size;
}

@mixin lineInner($width,$height,$color){
  content:'';
  @include fixedAll(absolute);
  @include rect($width,$height);
  background-color: $color;
  margin: auto;
}


@mixin tapSet($type, $val , $direction){
  #{$type-$direction}:$val;
  @if $direction {
    #{$type-$direction}:$val;
  }
  @else {
    #{type}:$val;
  }
}

.calContainerWidth{
  @media screen and (max-width:$small-screen-size) {
    width: 90%;
  }
  @media screen and (min-width:$small-screen-size) and (max-width:$middle-screen-size) {
    width: 70%;
  }
  @media screen and (min-width:$middle-screen-size) and (max-width:$big-screen-size) {
    width: 60%;
  }
  @media screen and (min-width:$big-screen-size) {
    width: 50%;
  }
}

.menu_root {
  position: fixed;
  z-index: $menus_zindex;
  top: 1em;
  right: 2em;
  width: 8em;
  height: 2.5em;
  border: 1px solid #c2c7cc;
  border-radius: 1.5em;
  color: antiquewhite;
  cursor: pointer;
  transition: box-shadow font-size .3s ease;
  will-change: box-shadow;
  &:hover {
    box-shadow: 0px 0px 1px 1px;
  }

  .button{
    position: absolute;
    padding: 0 1em;
    height: 100%;
    line-height: 2.5em;
    & > * {
      display: inline-block;
    }
    & > div {
      position: absolute;
    }
    img {
      @include square(2em);
      margin-top: 0.25em;
      -webkit-animation: rightan 1s infinite;
      -webkit-animation-fill-mode: both;
    }
  }

  .rightbar{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: auto;
    z-index:$backdrop_zindex; 
    background: #f2f2f0;
    width: 15%;
    min-width: 200px;

   // @include transform(translate(100%,0,0));


    .title{
      color:#353a48;
      padding: 1.5em 3em;
      font-size: 1.2em;
      font-weight: bold;
    }

    .close{
      position: absolute;
      top: 1em;
      right: 1em;
    }

    .item{
      &>div{
        padding: 2em 1em 1.5em;
        border-bottom: 1px solid #c4cbd0;
        color: #575f67;
        margin: 0 1em;
        font-size: 1.1em;
        &>img{
          @include square(1em);
          vertical-align: bottom;
          margin-right: 0.5em;
        }
      }
    }
  }

  :global(.slide-enter){
    transform: translateX(100%);
  }
  :global(.slide-enter-active) {
    transform: translateX(0%);
    transition: transform 300ms ease;
  }
  :global(.slide-exit){
    transform: translateX(0%);
  }
  :global(.slide-exit-active) {
    transform: translateX(100%);
    transition: transform 300ms ease;
  }
  
}



@-webkit-keyframes rightan {
  0% {
    bottom: 0%;
    opacity: 0;
  }
  50% {
    bottom: 5%;
    opacity: 1;
  }
}
