@mixin fixedAll($position) {
  position: $position;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

@mixin positionMiddle($position) {
  position: $position;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}

@mixin transform($transformValue) {
  transform: $transformValue;
  -moz-transform: $transformValue;
  -webkit-transform: $transformValue;
  -o-transform: $transformValue;
}

@mixin flexMiddle {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin textellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@mixin flex-fix() {
  -ms-flex: 1 1 0.000000001px;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  -webkit-flex-basis: 0.000000001px;
  flex-basis: 0.000000001px;
  width: 1%;
}

@mixin square($size) {
  width: $size;
  height: $size;
}

@mixin rect($width, $height) {
  width: $width;
  height: $height;
}

@mixin fixSquare($size) {
  width: $size;
  padding-bottom: $size;
}

@mixin lineInner($width,$height,$color){
  content:'';
  @include fixedAll(absolute);
  @include rect($width,$height);
  background-color: $color;
  margin: auto;
}


@mixin tapSet($type, $val , $direction){
  #{$type-$direction}:$val;
  @if $direction {
    #{$type-$direction}:$val;
  }
  @else {
    #{type}:$val;
  }
}

.calContainerWidth{
  @media screen and (max-width:$small-screen-size) {
    width: 90%;
  }
  @media screen and (min-width:$small-screen-size) and (max-width:$middle-screen-size) {
    width: 70%;
  }
  @media screen and (min-width:$middle-screen-size) and (max-width:$big-screen-size) {
    width: 60%;
  }
  @media screen and (min-width:$big-screen-size) {
    width: 50%;
  }
}

.article_item{
  padding: 50px 0 80px;
  border-bottom: 1.3px solid #c5cddd;
  &>header{
    margin-bottom: 20px;
    &>section{
      &:first-child{ // title
        font-size: 28px;
        font-weight: bold;
        cursor: pointer;
      }
      &:last-child{ // intro
        font-size: 12px;
        font-family: Fira Sans,sans-serif;
        color: #92969c;
        margin: 10px 0;
        &>div{
          display: inline-block;
        }
      }
    }
  }

  &>article{
    font-size: 17px;
    color: #5b6068;
    line-height: 1.5em;
    text-indent: 1em;
  }
}